:root {
  --primary-color: #0078d4;
  --primary-hover: #106ebe;
  --secondary-color: #f3f3f3;
  --text-color: #333;
  --border-color: #e0e0e0;
  --error-color: #d83b01;
  --success-color: #107c10;
  --shadow: 0 2px 8px #00000026;
  --border-radius: 6px;
  --transition: all .2s ease;
}

body {
  color: var(--text-color);
  background-color: #f9f9f9;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

.email-preview-container {
  box-sizing: border-box;
  flex-direction: column;
  gap: 16px;
  height: 100vh;
  padding: 20px;
  display: flex;
}

.app-header {
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.app-title {
  color: var(--primary-color);
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.directory-input {
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  background-color: #fff;
  align-items: center;
  margin-bottom: 10px;
  padding: 12px;
  display: flex;
}

.directory-input input {
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  transition: var(--transition);
  outline: none;
  flex: 1;
  padding: 10px 12px;
  font-size: 14px;
}

.directory-input input:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px #0078d433;
}

.directory-input button {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: var(--transition);
  border: none;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  padding: 10px 16px;
  font-weight: 500;
  display: flex;
}

.directory-input button:hover {
  background-color: var(--primary-hover);
}

.panel-container {
  flex: 1;
  gap: 16px;
  height: calc(100vh - 150px);
  display: flex;
}

.sidebar {
  border-radius: var(--border-radius);
  width: 300px;
  box-shadow: var(--shadow);
  background: #fff;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.panel-header {
  border-bottom: 1px solid var(--border-color);
  background-color: var(--secondary-color);
  color: var(--text-color);
  padding: 12px 16px;
  font-weight: 500;
}

.email-list {
  flex-direction: column;
  flex: 1;
  padding: 8px;
  display: flex;
  overflow-y: auto;
}

.email-item {
  cursor: pointer;
  border-radius: var(--border-radius);
  transition: var(--transition);
  align-items: center;
  margin-bottom: 4px;
  padding: 10px 12px;
  font-size: 14px;
  display: flex;
  position: relative;
}

.email-item:hover {
  background-color: var(--secondary-color);
}

.email-item.selected {
  background-color: var(--primary-color);
  color: #fff;
  font-weight: 500;
}

.email-item.selected:hover {
  background-color: var(--primary-hover);
}

.email-item:before {
  content: "üìß";
  margin-right: 8px;
  font-size: 16px;
}

.email-name {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.preview-container {
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  background: #fff;
  flex-direction: column;
  flex: 1;
  display: flex;
  overflow: hidden;
}

.preview-panel {
  background-color: #fff;
  flex: 1;
  position: relative;
  overflow: hidden;
}

.preview-panel iframe {
  background: #fff;
  border: none;
  width: 100%;
  height: 100%;
}

.empty-state {
  color: #666;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 20px;
  display: flex;
}

.empty-state-icon {
  color: var(--border-color);
  margin-bottom: 16px;
  font-size: 48px;
}

.empty-state-text {
  max-width: 300px;
  font-size: 16px;
  line-height: 1.5;
}

.error-message {
  color: var(--error-color);
  border-radius: var(--border-radius);
  background-color: #d83b011a;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px 12px;
  font-size: 14px;
  display: flex;
}

.error-message:before {
  content: "‚ö†Ô∏è";
  margin-right: 8px;
}

.loading {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

.loader {
  border: 4px solid #0078d41a;
  border-left-color: var(--primary-color);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: 1s linear infinite spin;
}

.loader-small {
  vertical-align: middle;
  border: 2px solid #ffffff4d;
  border-left-color: #fff;
  border-radius: 50%;
  width: 14px;
  height: 14px;
  margin-right: 8px;
  animation: 1s linear infinite spin;
  display: inline-block;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.tooltip {
  cursor: pointer;
  margin-left: 8px;
  display: inline-block;
  position: relative;
}

.tooltip-icon {
  background-color: var(--border-color);
  width: 20px;
  height: 20px;
  color: var(--text-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: bold;
  display: inline-flex;
}

.tooltip-text {
  visibility: hidden;
  color: #fff;
  text-align: center;
  z-index: 1;
  opacity: 0;
  pointer-events: none;
  background-color: #333;
  border-radius: 6px;
  width: 200px;
  padding: 8px;
  font-size: 12px;
  transition: opacity .3s;
  position: absolute;
  top: 125%;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2px 5px #0003;
}

.tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}
